<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getSearchInfo"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-table :data="infos" highlight-current-row v-loading="listLoading"
                  @selection-change="selsChange"
                  style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <!--<el-table-column prop="id" label="" width="60">-->
            <!--</el-table-column>-->
            <el-table-column prop="typeTitle" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="title" label="" min-width="250" sortable>
            </el-table-column>
            <el-table-column prop="picUrl" label="" width="120" sortable>
                <template scope="scope">
                    <img  v-if="scope.row.picUrl" :src="scope.row.picUrl" class="preview" alt="">
                </template>
            </el-table-column>
            <el-table-column prop="url" label="" min-width="160" sortable>
            </el-table-column>
            <el-table-column prop="visit" label="" min-width="80" sortable>
            </el-table-column>
            <el-table-column prop="updateTime" label="" min-width="100" :formatter="formatUpdateTime" sortable>
            </el-table-column>
            <el-table-column prop="releaseTime" label="" min-width="100" :formatter="formatReleaseTime" sortable>
            </el-table-column>
            <el-table-column label="" width="120">
                <template scope="scope">
                    <!--<el-button size="small" @click="goPath"></el-button>-->
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="total, sizes, prev, pager, next, jumper"
                           @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page.sync="page"
                           :page-sizes="pageSizesList"
                           :page-size="pageSize"
                           :total="totalDataNumber"
                           style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<style>
    .preview {
        width: 35px;
        height: 35px;
        display: block;
    }
</style>

<script>
    import util from '../../common/js/util'
    import api from '../../common/js/interface'
    import {mapGetters} from 'vuex'
    import {mapActions} from 'vuex'
    //import NProgress from 'nprogress'
    // import {getUserListPage, removeUser, batchRemoveUser, editUser, addUser} from '../../api/api';

    export default {
        components: {UE},
        data() {
            return {
                // serverUrl :'syb',  //
                // serverUrl :'..',  //
                serverUrl: process.env.API_ROOT,  //
                filters: {
                    title: '',
                    note: ''
                },
                infos: [],
                typeValue: 0,
                users: [],
                page: 1,
                pageSize: 20,
                pageSizesList: [1, 5, 10, 15, 20, 30, 50, 100],
                totalDataNumber: 0,//,
                listLoading: false,
                sels: [],//

                defaultMsg: 'UE',
                config: {
                    initialFrameWidth: null,
                    initialFrameHeight: 350
                },
                ue1: "ue1", // id
            }
        },
        computed: {
            //  getters  computed 
            ...mapGetters([
                'getType'
                // ...
            ])
        },
        methods: {
            ...mapActions([
                'editInfo'
            ]),
            //
            formatType: function (row, column) {
                let label = '';
                // console.log("row.type:", row.type);
                this.types.forEach(function (item, index) {
                    if (row.type === item.value) {
                        label = item.label;
                    }
                });
                return label;
            },
            //JSON
            formatUpdateTime: function (row, column) {
                return util.formatDate(row.updateTime, "yyyy-MM-dd");
            },
            formatReleaseTime: function (row, column) {
                return util.formatDate(row.releaseTime, "yyyy-MM-dd");
            },
            //
            handleCurrentChange(val) {
                this.page = val;
                this.getInfos();
            },
            //
            handleSizeChange(val) {
                let _this = this,
                    pageSize = `${val}`;
                this.page = 1;
                this.pageSize = parseInt(pageSize);
                console.log('pageSize: ' + pageSize);
                this.$nextTick(() =>
                    api.Infos.limitInfosByTypeAndCurrentPageAndPageSize(_this.typeValue, page, pageSize, function (res) {
                        // console.log(JSON.stringify(res));
                        _this.totalDataNumber = pageSize * res.body.data.totalPage;
                        return new Promise((resolve, reject) => {
                            // setTimeout()
                        })
                    })
                )
            },
            //
            getSearchInfo(){
                let para = {
                    title: this.filters.title,
                    note: this.filters.title
                };
                let _this = this,
                    page = this.page,
                    pageSize = this.pageSize;
                this.listLoading = true;
                console.log(JSON.stringify(para));
                api.Infos.searchInfosByTitleOrNote(JSON.stringify(para), _this.typeValue, page, pageSize, function (res) {
                    _this.totalDataNumber = pageSize * res.body.data.totalPage;
                    _this.infos = res.body.data.resultList;
                    _this.infos.forEach(function (item, index) {
                        if (item.picUrl) {
                            item.picUrl = _this.serverUrl + "\\images\\" + item.picUrl;
                        }
                    });
                    _this.listLoading = false;
                    return new Promise((resolve, reject) => {
                        // setTimeout()
                    })
                })
            },

            //
            getInfos() {
                let _this = this,
                    page = this.page,
                    pageSize = this.pageSize;
                this.listLoading = true;
                api.Infos.limitInfosByTypeAndCurrentPageAndPageSize(_this.typeValue, page, pageSize, function (res) {
                    _this.totalDataNumber = pageSize * res.body.data.totalPage;
                    _this.infos = res.body.data.resultList;
                    _this.infos.forEach(function (item, index) {
                        if (item.picUrl) {
                            item.picUrl = _this.serverUrl + "\\images\\" + item.picUrl;
                        }
                    });
                    _this.listLoading = false;
                    return new Promise((resolve, reject) => {
                        // setTimeout()
                    })
                })
            },
            removeInfo: function (id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    api.Infos.deleteInfo(id, function (res) {
                        if (res.body.status) {
                            resolve(res);
                        } else {
                            reject();
                        }
                    });
                });
            },
            //
            handleDel: function (index, row) {
                let _this = this;
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let infoId = row.id;
                    _this.removeInfo(infoId).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        _this.getInfos();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                console.log("row:", row.id);
                // this.editForm = Object.assign({}, row);
                // this.editForm = Object.assign({}, row);
                let info_id = row.id;
                this.editInfo(info_id);
                this.$router.push({path: '/edit'});
            },
            //
            handleAdd: function () {
                this.$router.push({path: '/add'});
            },
            // //
            // editSubmit: function () {
            //     this.$refs.editForm.validate((valid) => {
            //         if (valid) {
            //             this.$confirm('', '', {}).then(() => {
            //                 this.editLoading = true;
            //                 //NProgress.start();
            //                 let para = Object.assign({}, this.editForm);
            //                 para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
            //                 editUser(para).then((res) => {
            //                     this.editLoading = false;
            //                     //NProgress.done();
            //                     this.$message({
            //                         message: '',
            //                         type: 'success'
            //                     });
            //                     this.$refs['editForm'].resetFields();
            //                     this.editFormVisible = false;
            //                     this.getUsers();
            //                 });
            //             });
            //         }
            //     });
            // },
            // //
            // addSubmit: function () {
            //     this.$refs.addForm.validate((valid) => {
            //         if (valid) {
            //             this.$confirm('', '', {}).then(() => {
            //                 this.addLoading = true;
            //                 //NProgress.start();
            //                 let para = Object.assign({}, this.addForm);
            //                 para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
            //                 addUser(para).then((res) => {
            //                     this.addLoading = false;
            //                     //NProgress.done();
            //                     this.$message({
            //                         message: '',
            //                         type: 'success'
            //                     });
            //                     this.$refs['addForm'].resetFields();
            //                     this.addFormVisible = false;
            //                     this.getUsers();
            //                 });
            //             });
            //         }
            //     });
            // },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    // this.listLoading = true;
                    // //NProgress.start();
                    // let para = {ids: ids};
                    // batchRemoveUser(para).then((res) => {
                    //     this.listLoading = false;
                    //     //NProgress.done();
                    //     this.$message({
                    //         message: '',
                    //         type: 'success'
                    //     });
                    //     this.getUsers();
                    // });
                }).catch(() => {

                });
            }
        },
        watch: {
            "$route": function (to, from) {
                let _this = this;
                console.log(to, from);
                let hash = to.path;
                hash = hash.split('/')[1];
                //from 
                //to 
                //nextnext()next(),
                this.types.forEach(function (item, index) {
                    if (hash === item.url) {
                        _this.typeValue = item.value;
                        _this.typeValue = parseInt(_this.typeValue);
                    }
                });
                console.log(_this.typeValue);
                this.getInfos();
            }
        },
        mounted() {
            let _this = this;
            this.types = this.getType;

            let hash = window.location.hash;
            hash = hash.split('/')[1];
            this.types.forEach(function (item, index) {
                if (hash === item.url) {
                    _this.typeValue = item.value;
                    _this.typeValue = parseInt(_this.typeValue);
                }
            });
            console.log(_this.typeValue);
            this.getInfos();
        }
    }

</script>

<style scoped lang="scss" type="text/scss">
    .el-table__row .cell {
        font-size: 13px;
    }
</style>


