import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex);

// 
const state = {
    count: 10,
    editForm: [],
    infoId: 0,
    picId: 0,
    projectId:0,
    type: [
        {value: 1, label: '', url: 'activity'},
        {value: 2, label: '', url: 'news'},
        {value: 3, label: '', url: 'knowledge'},
        {value: 4, label: '', url: 'team'},
        {value: 5, label: '', url: 'alumni'},
        {value: 6, label: '', url: 'tutor'},
        {value: 7, label: '', url: 'train'},
        {value: 8, label: '', url: 'policy'},
        {value: 9, label: '', url: 'fund'},
        {value: 10, label: '', url: 'enter'},
        {value: 11, label: '', url: 'manage'},
        {value: 12, label: '', url: 'assess'},
        {value: 13, label: '', url: 'hatch'},
        {value: 14, label: '', url: 'base'}],
    loginUser: {}
};

//  mutations
const mutations = {
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
    EDIT_INFO(state, id) {
        // state.editForm = arr;
        state.infoId = id;
    },
    EDIT_PIC(state, id) {
        state.picId = id;
    },
    EDIT_PROJ(state, id){
        state.projectId = id;
    },
    LOGINUSER(state, user){
        state.loginUser = user;
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


